<template>
  <div class="container">
    <div class="row justify-content-center">
      <div class="col-md-6">
        <h1>Editar</h1>
        <form @submit.prevent="actualizarData(nota)">
          <div class="form-group">
            <!-- <input type="hidden" v-model="nota.id" /> -->
            <label for="nombre">Nombre</label>
            <input
              type="text"
              class="form-control"
              placeholder="Ingrese el nombre"
              v-model="nota.nombre"
            />
          </div>
          <div class="form-group">
            <label for="descripcion">Descripcion</label>
            <textarea
              class="form-control"
              cols="30"
              v-model="nota.descripcion"
              rows="2"
              placeholder="Ingrese una descripcion"
            ></textarea>
          </div>
          <div class="form-group mt-3">
            <button type="submit" class="btn btn-primary btn-block">
              GUARDAR
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "Editar",
  data() {
    return {
      nota: {
        // id: "",
        nombre: "",
        descripcion: "",
      },
    };
  },
  methods: {
    obtenerData() {
      const _idParam = this.$route.params.id;
      this.axios
        .get(`notas/${_idParam}`)
        .then((res) => {
          const { nombre, descripcion, _id } = res.data;
          //   this.nota.id = _id;
          this.nota.nombre = nombre;
          this.nota.descripcion = descripcion;
        })
        .catch((err) => console.log(err));
    },
    actualizarData() {
      const _idParam = this.$route.params.id;
      this.axios
        .put(`notas/${_idParam}/editar`, this.nota)
        .then((res) => {
          this.$swal("Actualizado", "", "success");
          this.$router.push({ name: "Notas" });
        })
        .catch((err) => console.log(err));
    },
  },
  created() {
    this.obtenerData();
  },
};
</script>